// BASIC
import React, { Component } from 'react'
import styled, {css} from 'styled-components'
import {db, au} from '../../../Config/firebase'
import latinize from 'latinize'
import camelcase from 'camelcase'
import {paramCase} from 'change-case'
// COMPONENTS
import Specification from './components/Specification'
import Editor from './components/Editor'
import WritingApp from './components/WritingApp'

const WordListPage = styled.div`
	/* width: 100vw; */
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	overflow-y: scroll;
	font-size: 20px;
`
const TableHead = styled.thead``
const Th = styled.th``
const UnitName = styled.th``
const TableBody = styled.tbody``
const Td = styled.td``
const Tr = styled.tr``
const Image = styled.img``
const Item = styled.span``
const ContentTable = styled.table`
	position: relative;
	border-collapse: collapse;
	margin: 25px 0;
	width: 90vw;
	max-width: 1300px;
	margin: 0 auto;
	text-align: center;
	@media (max-width: 600px) {
		width: 100vw;
	}
	${TableHead} ${Th},
	${TableHead} ${UnitName} {
		background-color: #009878;
		font-weight: bold;
		position: sticky;
		top: 0;
		z-index: 2;
	}
	${TableHead} ${Tr} {
		background-color: #009878;
		font-weight: bold;
	}
	${Th}, ${Td} {
		@media(max-width: 1100px) {
			:nth-child(3) {
				display: none;
			}
		}
		@media (max-width: 1000px) {
			:nth-child(4) {
				display: none;
			}
		}
		@media (max-width: 600px) {
			height: 70px;
			:nth-child(5) {
				display: none;
			}
		}
		@media (max-width: 500px) {
			height: 60px;
			:nth-child(2) {
				display: none;
			}
		}
	}
	${Th} {
		@media (max-width: 1000px) {
			:nth-child(1) {
				display: none;
			}
			:nth-child(2) {
				display: none;
			}
			:nth-child(5) {
				display: none;
			}
			:nth-child(4) {
				display: none;
			}
		}
	}
	${TableHead} ${UnitName} {
		background-color: #11a989;
		@media (max-width: 1000px) {
			padding: 7px 30px;
			border-bottom-left-radius: 50px;
			border-bottom-right-radius: 50px;
		}
	}
	${Th},
	${UnitName},
	${Td} {
		width: 20%;
		padding: 12px 15px;
		::first-letter {
			text-transform: capitalize;
		}
		:first-child {
			text-transform: capitalize;
		}
	}
	${TableBody} ${Tr} {
		border-bottom: 1px solid var(--color-primary);
		transition: all 0.3s ease;
		:hover {
			opacity: 0.5;
			cursor: pointer;
		}
	}
	${TableBody} ${Tr}:nth-of-type(even) {
		background-color: #303030;
	}
	${TableBody} ${Tr}:last-of-type {
		border-bottom: 2px solid #009878;
	}
	${Image} {
		width: 141px;
		height: 100px;
		border-radius: 5px;
	}
`

class WordList extends Component {
	state = {
		units: [],
		unitWords: [],
		unitsLength: {},
		wordData: null,
		activeWord: '',
		info: {
			unit: 0,
			part: 0
		}
	}
	componentDidMount() {
		let {unitNumber, unitName} = this.props.match.params;
		if (unitNumber.includes('.') && Number(unitNumber)) {
			this.setState({
				info: {
					unit: Number(unitNumber.replace(/\.[0-9]*/g, '')),
					part: Number(unitNumber.replace(/[0-9]*\./g,''))
				}
			})
			unitNumber = Number(unitNumber.replace(/\.[0-9]*/g, ''))
		}
		else if (Number(unitNumber)) {
			this.setState({
				info: {
					unit: Number(unitNumber),
					part: 0
				}
			})
			unitNumber = Number(unitNumber)
		}
		else {
			alert('nie ma strony!');
		}
		au.onAuthStateChanged(user => {
			if(user) {
				db.collection('books').onSnapshot(snaps => {
					const units = [];
					snaps.forEach(snap => {
						if (snap.data().info.id.includes(this.props.match.params.bookName)) {
							Object.keys(snap.data()).sort().forEach((unit) => {
								if (unit !== 'info') {
									units.push(snap.data()[unit])
								}
							})
						}
					})
					const unitWords = []
					Object.keys(units[unitNumber - 1].parts).sort().forEach((part) => {
						console.log('i', part);
						unitWords.push(units[unitNumber - 1].parts[part])
					})
					this.setState({units, unitWords});
					const element = document.getElementById(unitName);
					if (element) {
						element.scrollIntoView();
					}
				})
			}
		})
	}
	setData = (word1, word2, word3, translation1, translation2, translation3, female, level, type, image, partIndex, wordIndex, e) => {
		this.setState({
			wordData: {
				word1, word2, word3, translation1, translation2, translation3, female, level, type, image, partIndex, wordIndex,
				bookName: this.props.match.params.bookName,
				unitNumber: this.props.match.params.unitNumber
			}
		})
		console.log('id: ', e.currentTarget.id);
		if (e.currentTarget.id) {
			this.setState({
				activeWord: e.currentTarget.id.replace(/word-/, '')
			})
		}
	}
	updateData = (name, data) => {
		this.setState(prevState => ({
			wordData: {
				...prevState.wordData,
				[name]: data
			}
		}));
	}
	clearData = () => {
		this.setState({
			wordData: null
		})
	}
	goToPrevious = () => {
		const activeWord = this.state.activeWord.split('-');
		const previousId = `word-${activeWord[0]}-${Number(activeWord[1]) - 1}`;
		if (document.getElementById(previousId)) {
			document.getElementById(previousId).click()
			this.setState({activeWord: previousId.replace(/word-/, '')})
		}
	}
	goToNext = () => {
		const activeWord = this.state.activeWord.split('-');
		const nextId = `word-${activeWord[0]}-${Number(activeWord[1]) + 1}`;
		if (document.getElementById(nextId)) {
			document.getElementById(nextId).click()
			this.setState({activeWord: nextId.replace(/word-/, '')})
		}
	}
	render() {
		let unitsLength = {};
		return (
			<WordListPage>
				<>
				{this.state.unitWords.length === 0 ? 'Wczytywanie...' : <>
					{this.state.unitWords.map(({name, words}, partIndex) => {
					unitsLength[`part_${(partIndex + 1).toString().padStart(2, '0')}`] = Object.values(words).length;
					return (
						<ContentTable key={name} id={paramCase(latinize(name))}>
							<TableHead>
								<Tr>
									<UnitName colSpan="5">{name}</UnitName>
								</Tr>
								<Tr>
									<Th>Słówko</Th>
									<Th>Tłumaczenie</Th> 
									<Th>Poziom</Th>
									<Th>Rozdział</Th>
									<Th>Zdjęcie</Th>
								</Tr>
							</TableHead>
							<TableBody>
								{Object.values(words).map(({word1, word2, word3, translation1, translation2, translation3, female, level, type, image}, wordIndex) => {
									return (
										<Tr id={`word-${partIndex + 1}-${wordIndex}`} key={`word-${partIndex + 1}-${wordIndex}`} onClick={(e) => this.setData(word1, word2, word3, translation1, translation2, translation3, female, level, type, image, partIndex, wordIndex, e)}>
											<Td className={camelcase(word1)}>
												{word3 ? <>
													<Item>{word1}</Item> / <Item>{word2}</Item> / <Item>{word3}</Item>
												</> : word2 ? <>
													<Item>{word1}</Item> / <Item>{word2}</Item>
												</> : <>
													<Item>{word1}</Item>
												</>}
											</Td>
											<Td className={camelcase(word1)}>{translation1}</Td> 
											<Td className={camelcase(word1)}>{level === 'basic' ? 'Podstawowy': 'Rozszerzony'}</Td>
											<Td className={camelcase(word1)}>{type}</Td>
											<Td className={camelcase(word1)}>
												<Image src={image !== 'url' ? image : `https://fakeimg.pl/647x400/?text=${word1}`} alt="" height="100px" />
											</Td>
										</Tr>
									)
								})}
							</TableBody>
						</ContentTable>
					)})}
				</>}
				</>
				{this.state.wordData && <WritingApp data={this.state.wordData} unitsLength={unitsLength} goToNext={this.goToNext} goToPrevious={this.goToPrevious} onBack={this.clearData} updateData={this.updateData} />}
			</WordListPage>
		);
	}
}

export default WordList